import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { isValidArray }            from "Helpers";
import { useHandlerMutation }      from "Helpers/Hooks";
import { AppraisalHOCPageFetcher } from "Components/Appraisal";
import AppraisalSection5           from "./AppraisalSection5";

const PARAMS =  { select_fields : ["page_five"] };
const DEFAULT_FETCH_DATA = { page_five : { finish : {}, installation : {}, structure : {} } };
const ARCHITECTURAL_FINISHES = [
	{
		name   : "",
		floors : "",
		walls  : "",
		roof   : "",
	},
];

const AppraisalSection5Container = ({ appraise = DEFAULT_FETCH_DATA}) => {
	const { id : appraiseId } = useParams();

	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const { page_five } = appraise;
	const { finish }    = page_five;

	// ------------> hookForm <---------------
	const getValidArchitecturalFinishes = () => isValidArray(finish.architectural_finishes)
		? finish?.architectural_finishes : ARCHITECTURAL_FINISHES;


	const defaultValues = {
		...page_five,
		finish : {
			...finish,
			architectural_finishes : getValidArchitecturalFinishes(),
		},
	};

	const hookForm = useForm({
		defaultValues,
		shouldUnregister : true,
	});

	const { handleSubmit } = hookForm;

	const onSubmit =  handleSubmit(async (formData) => {
		await editApprasial({
			module : `/appraisals/page-five/${appraiseId}`,
			method : "PUT",
			data   : formData,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<AppraisalSection5
			delegations={{
				onSubmit,
				hookForm,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(AppraisalSection5Container, PARAMS);
