import {
	Tabs,
	Finishes,
	Structure,
	HeaderPage,
	Installations,
} from "Components/Appraisal";

const tabs = [
	{
		title : "Estructura",
		Tab   : Structure,
	},
	{
		title : "Acabados",
		Tab   : Finishes,
	},
	{
		title : "Instalaciones",
		Tab   : Installations,
	},
];

const AppraisalSection5 = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
	},
}) => {
	const { formState : { isDirty } } = hookForm;

	return (
		<>
			<HeaderPage
				currentIndex={[1, 2]}
				section="body"
				isDirty={isDirty}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>
			<Tabs
				tabs={tabs}
				hookForm={hookForm}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
				section="2.4 Elementos de ConstrucciÃ³n"
			/>
		</>
	);
};

export default AppraisalSection5;
